import { ETcfGvlDisclosureType } from "@devowl-wp/real-cookie-banner-backend/src/types/service/tcf";

function getLocalizedTcfCookieType(type) {
  switch (type) {
    case ETcfGvlDisclosureType.Cookie:
      return "HTTP Cookie";

    case ETcfGvlDisclosureType.Web:
      return "LocalStorage, Session Storage, IndexDB";

    case ETcfGvlDisclosureType.App:
      return "App";

    default:
      return type;
  }
}

export { getLocalizedTcfCookieType };